* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Staatliches", cursive;
  letter-spacing: 1px;
}

:root {
  --text-base: rgb(49, 49, 49);
  --color-base: rgb(92, 179, 107);
  --gradient: linear-gradient(145deg, rgb(92, 179, 107), rgb(94, 177, 170));
}

body {
  background-color: rgb(255, 238, 217);
}

.header {
  background: var(--gradient);
  box-shadow: 0 5px rgba(0, 0, 0, 0.1);
}

.quote {
  text-align: center;
  padding: 30px;
  color: whitesmoke;
  letter-spacing: 4px;
  text-shadow: 2px 2px black;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 50px 0;
}

/* Basic nav-button - disabled, keine Interaktion */
.nav-button {
  padding: 10px 0;
  width: 200px;
  background: none;
  border: 2px solid lightgrey;
  outline: none;
  color: grey;
  font-size: 1.2em;

  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}

.nav-button:nth-child(2) {
  margin: 0 15px;
}

.nav-button-enabled {
  border: 2px solid var(--color-base);
  color: var(--text-base);
  box-shadow: 5px 5px rgba(0, 0, 0, 0.1);
}

.nav-button-active {
  background: var(--color-base);
  color: whitesmoke;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.1);
  border: 2px solid var(--color-base);
}

.nav-button-enabled:hover {
  cursor: pointer;
  color: whitesmoke;
  background-color: var(--color-base);
  transform: translateY(2px);
}

.heading {
  text-align: center;
  margin-bottom: 30px;
  color: var(--text-base);
}

.todo-container {
  display: grid;
  grid-template-rows: repeat(auto-fill, auto);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
  align-items: center;
  margin: 0 5vw;
}

.todo {
  height: 60px;
  background: var(--gradient);
  border-radius: 8px;
  padding: 15px;
  border: 2px solid rgb(3, 83, 0);

  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.1);
}

.todo-completed {
  background: linear-gradient(145deg, rgb(154, 180, 158), rgb(153, 182, 179));
  box-shadow: none;
  border-color: transparent;
}

.todo-completed > .todo-text {
  text-decoration: line-through;
  color: whitesmoke;
}

.todo-checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  appearance: none;
  height: 25px;
  width: 25px;
  border: 2px solid whitesmoke;
  background-color: rgb(188, 233, 188);
  transition: background-color 0.3s;
}

.todo-checkbox:hover {
  cursor: pointer;
  background-color: whitesmoke;
  border-color: whitesmoke;
}

.todo-checkbox:checked::after {
  content: "\2714";
  font-size: 2em;
  color: rgb(21, 94, 128);
}

.todo-text {
  margin: 0 20px;
  flex: 1;
  background: none;
  border: none;
  outline: none;
  border-bottom: 2px solid transparent;
  color: whitesmoke;
  font-size: 1em;
}

.todo-text:focus {
  border-color: lightgrey;
}

.delete-todo-button {
  color: whitesmoke;
  padding: 2px 5px;
  font-size: 1em;
  background: none;
  border: 2px solid whitesmoke;
  border-radius: 5px;
  transition: background-color 0.3s, border-color 0.3s;
}

.delete-todo-button:hover {
  cursor: pointer;
  background-color: rgb(173, 65, 65);
  border-color: rgb(173, 65, 65);
}

.new-note-button {
  height: 30px;

  background: none;
  border: 3px dashed grey;
  border-radius: 5px;
  outline: none;
  color: var(--text-base);
  transition: color 0.3s, border-color 0.3s;
}

.new-note-button:hover {
  cursor: pointer;
  color: var(--color-base);
  border-color: var(--color-base);
}

footer {
  text-align: center;
  margin-top: 20px;
  color: var(--text-base);
}

/* MEDIA QUERIES */
@media (max-width: 1500px) {
  .todo-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1100px) {
  .todo-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 720px) {
  .todo-container {
    grid-template-columns: 1fr;
  }

  .quote {
    font-size: 1.2em;
    padding: 10px;
  }
}

@media (max-width: 650px) {
  .nav-button {
    margin: 10px;
  }

  .todo-container {
    margin: 0;
    grid-gap: 15px;
  }

  .heading {
    margin-bottom: 20px;
  }

  nav {
    margin: 10px 0;
  }
}
